import React, { Component } from 'react'
import { Form, Button, Col, Row, InputGroup, Dropdown, Badge, Image } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import ImageType from '../../../CommonComponents/MediaType/ImageType';
import { Link } from 'react-router-dom'
import { connect } from 'react-redux';
import { ACTION_PRODUCT_LIST_FORTAG, PRODUCT_TAGS_AUTOSEARCH, fetchProductList } from '../../../../../../actions/productAction';
import { REMOVE_TAGGED_PRODUCT_ACTION, TAG_PRODUCT_ACTION } from '../../../../../../actions/moderationActions';
import { SEARCH_BY_PRODUCTS } from '../../../../../../reducer/keys';
import { getUserInfo_afterChanges } from '../../../../../../actions/loginActions';
import { TOAST_ERROR, check_hotspotLocation } from '../../../../../../utils';
import { checkPermission } from '../../../../../../actions/collaboratorActions';
import { Loader } from '../../../CommonComponents/Loader';

class TagProducts extends Component {

    constructor(props) {
        super(props);
        this.state = { tagedprod: [], showProd: false, showtags: false, searchTag: '', loadingTag: false, productTags: [], selectedTag: [], productId: 0, errorMsg: '', searchText: '', currentPage: 1, loading: false, scrolledToBottom: false, sentReq: false, visenzeId: null, visenzeProduct: null, firstTime: false, productList:[] }
        this.outsideNewRef = React.createRef();
        this.outsideNewRef2 = React.createRef();
        this.listRef = React.createRef();
        this.tagProductRef = React.createRef();
    }

    componentDidMount() {
        window.addEventListener('click', this.handleClickOutside);
        window.addEventListener('click', this.handleClickOutsideTagProduct);

        window.addEventListener('scroll', this.handleScroll);
        // this.setLastSearchTag();
    }

    setLastSearchTag = () => {
        const { modalData } = this.props;
        this.setState({loading:true})
        this.props.fetchProductList(1, 10, this.state.selectedTag, '',this.responceApi, modalData?.referenceId, modalData?.newMedia)
    }

    componentWillUnmount() {
        window.removeEventListener('click', this.handleClickOutside);
        if (this.searchTimeout) { clearTimeout(this.searchTimeout); }
        window.removeEventListener('scroll', this.handleScroll);
        window.addEventListener('click', this.handleClickOutsideTagProduct);
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.state.searchText !== prevState.searchText) {
            if (this.searchTimeout) { clearTimeout(this.searchTimeout); }
            this.searchTimeout = setTimeout(() => {
                this.triggerAction();
            }, 800);
        }
    }

    handleClickOutsideTagProduct = (event) => {
        if (this.tagProductRef && !this.tagProductRef?.current?.contains(event.target)) { this.setState({ showtags: false }); }
    };

    handleClickOutside = (event) => {
        if (this.outsideNewRef.current && !this.outsideNewRef.current?.contains(event.target)) this.setState({ showProd: false });
    };


    filterProduct = (e) => {
        const keyword = e.target.value;
        this.setState({ searchText: keyword });
    }

    triggerAction = () => {
        const { searchText, selectedTag } = this.state;
        const { SEARCH_PRODUCTS_TEXT, fetchProductList, modalData } = this.props;

        if (searchText?.length >= 1) {
            this.setState({ showProd: true, loading: true, productList:[] });
            SEARCH_PRODUCTS_TEXT(searchText);
            fetchProductList(1, 10, selectedTag, searchText, this.responceApi, modalData?.referenceId, modalData?.newMedia);
        } else if (!searchText) {
            this.setState({ showProd: true, loading: true, productList:[] });
            SEARCH_PRODUCTS_TEXT('');
            fetchProductList(1, 10, selectedTag, '', this.responceApi, modalData?.referenceId, modalData?.newMedia);
        }
    }

    responceApi = (data) => { 
        this.setState(prevState=>({ loading: false, productList:[...prevState.productList, ...data] })); 
    }

    removeTag = (tagToRemove) => {
        const updatedSelectedTag = this.state.selectedTag.filter(tag => tag !== tagToRemove);
        this.setState({ selectedTag: updatedSelectedTag, searchTag: '', showtags: false, productTags: [] }, () => this.props.fetchProductList(1, 10, this.state.selectedTag, '', this.responceApi, this.props.modalData?.referenceId, this.props.modalData?.newMedia))
    }

    product_tags_search = (e) => {
        const keyword = e.target.value;
        this.setState({ loadingTag: true, showtags: keyword?.length > 0 ? true : false })
        PRODUCT_TAGS_AUTOSEARCH(keyword).then((response) => {
            const{ responseCode, responseData, responseMessage } = response.data
                if (responseCode === 200) {
               this.setState({ productTags: responseData, showtags: responseData && responseData.length ? true : false, loadingTag: false })
                }
        })
        this.setState({ searchTag: keyword });
    }

    getData_bySelected_Tag = (tag) => {
        var tags = [...this.state.selectedTag, tag]
        this.setState({ selectedTag: [...new Set(tags)], showtags: false, searchTag: '' },
            () => {
                this.props.fetchProductList(1, 10, this.state.selectedTag, '', this.responceApi, this.props.modalData?.referenceId, this.props.modalData?.newMedia);
            }
        )
    }

    handleScroll = () => {
        const listElement = this.listRef.current;
        if (listElement) {
            const rect = listElement.getBoundingClientRect();
            if (rect.bottom <= window.innerHeight) {
                this.setState({ scrolledToBottom: true }, () => { this.filterProductOnScrolle(); });
            }
        }
    };

    addProduct = (product, referenceId) => {
        const { modalData } = this.props;
        let check_hot = modalData?.postproduct && modalData?.postproduct.length > 0 ? check_hotspotLocation(modalData) : '45%';
        const location_hotspot = { 'left': `45%`, 'top': `${check_hot}` }
        const hostpot = `left:45%;top:${check_hot};`
        this.props.TAG_PRODUCT_ACTION(product, referenceId, location_hotspot, hostpot, modalData?.feedId, this.callbackMsg)
        this.setState({ firstTime: true })
        this.setState({ showProd: false });
    }

    changePrdId = (id) => {
        this.setState({ productId: id })
    }

    callbackMsg = (msg) => {
        this.setState({ errorMsg: msg }, () => { setTimeout(() => { this.setState({ errorMsg: '' }) }, 5000) })
        TOAST_ERROR(msg)
    }


    filterProductOnScrolle = () => {
        const { total_pages } = this.props;
        if (this.state.currentPage < total_pages && !this.state.sentReq) this.setState({ currentPage: this.state.currentPage + 1, sentReq: true }, () => this.props.ACTION_PRODUCT_LIST_FORTAG(this.state.currentPage, 10, this.state.selectedTag, this.state.searchText, this.completedPageCall, this.props.modalData?.referenceId, this.props.modalData?.newMedia))
    }

    completedPageCall = (data) => this.setState(prevState=>({ sentReq: false, productList:[...prevState.productList, ...data] })); 


    removeTaggProduct = (product_sku, productId, referenceId) => { 
        if(checkPermission('contentGallery', 'editGallery', true)) this.props.REMOVE_TAGGED_PRODUCT_ACTION(product_sku, productId, referenceId);
    };


    render() {
        const { showProd, selectedTag, productTags, showtags, searchTag, loadingTag, errorMsg, searchText, sentReq, loading, productList } = this.state
        const { modalData, changeHotspotStatus, enableHotspot } = this.props
        return (<div className="">
            <div className='p-4 mb-3 mb-10'>
                {!(modalData.type == 5 || modalData.type == 3 || modalData.type == 1) ?
                    <Form.Group className='mb-3'>
                        <Form.Check type="switch" id="enable_hotspot" className="f-between" label="Enable Hotspot" defaultValue={enableHotspot} onChange={() => checkPermission('contentGallery', 'editGallery', true) ? changeHotspotStatus(modalData.hotspot, modalData.referenceId) : {}} checked={enableHotspot} reverse />
                    </Form.Group>
                    : ''}

                <Dropdown show={true} className='position-relative w-100 mb-3' align={'end'}>
                    <Dropdown.Toggle variant="outline-secondary" className={`arrow_disabled p-0 border-0 mb-0 flex-grow-1 w-100 bg-transparent`}>
                        <InputGroup>
                            <Form.Control id="search_pro_" className={errorMsg ? 'is-invalid' : ''} placeholder="Search Products By name, category, SKU, tags" ref={this.outsideNewRef} onFocus={() => this.setState({ showProd: !showProd, productList:[],currentPage:1 })} onChange={this.filterProduct} value={searchText} autoComplete='off' onClick={() => this.setLastSearchTag()} />
                            <InputGroup.Text>
                                <FontAwesomeIcon type={'solid'} icon={'magnifying-glass'} />
                            </InputGroup.Text>
                        </InputGroup>
                    </Dropdown.Toggle>
                    {errorMsg ? <span className="invalid-feedback">{errorMsg}</span> : null}
                    
                    { loading ? 
                        <Dropdown.Menu  show={showProd ? true : false} className='w-100'>
                            <div className='w-100' style={{ overflowX: 'auto', minHeight: '320px'}} >
                                <Loader bg={'white'}/>
                            </div>
                        </Dropdown.Menu> :
                        showProd && productList && productList.length > 0 ? <Dropdown.Menu show={showProd && productList ? true : false} className='w-100' >
                            <div className='w-100' style={{ overflowX: 'auto', maxHeight: 320 }} ref={this.listRef} onScroll={this.handleScroll}>
                                {productList.map((item, index) => {
                                    const proImage = item?.product_image?.includes('?') ? `https://cdn.taggbox.com/v7/${item?.product_image}&w=100` : `https://cdn.taggbox.com/v7/${item?.product_image}?w=100`
                                    return (<Dropdown.Item key={index} data-item="product" onClick={() => checkPermission('contentGallery', 'editGallery', true) ? this.addProduct(item, modalData.referenceId) : {}} className='d-flex'>
                                        <div className="w-64px d-flex flex-shrink-0 me-2">
                                            <ImageType mediaClass={'position-relative overflow-hidden w-100 border rounded-1'} size={100} height={64} width={64} url={proImage} alt={""} isCover={true} />
                                        </div>
                                        <div className="d-flex align-items-start flex-column overflow-hidden w-100">
                                            <p className="fs-9 fw-medium mb-1 text-wrap ">{item.product_title}</p>
                                            <span className="d-flex align-items-center tb_pro_price mb-1">
                                                {(item.product_price && item.product_price > 0) ?
                                                    <span className='fs-8 fw-bold'>{item.price_currency_symbol}{item.product_discount > 0 ? item.product_discount : item.product_price}</span>
                                                    :
                                                    ''}
                                                {(item.product_price && item.product_discount > 0 && item.product_discount < item.product_price) ?
                                                    <span className='fs-8 ms-2 text-muted' style={{ textDecoration: 'line-through' }}>{item.price_currency_symbol}{item.product_price}</span>
                                                    : null}
                                            </span>
                                            {item.recomended ? 
                                            <div className='f-between mt-2 w-100'>
                                                    <Badge bg='primary' className='rounded-pill bg-transparent text-primary border border-primary'><FontAwesomeIcon icon={'sparkles'} iconClass={'me-1'} /> Ai-Recommended</Badge>
                                                    <div className='position-relative' style={{marginRight:-5}}>
                                                        <Image src={baseUrl('media/svg/trusense.svg')} height={20} width={100} alt='trueSense' fluid/>
                                                    </div>
                                                </div>
                                            :
                                            <p className='fs-10 text-muted mb-0'>
                                                {item.shopify_product_type ? <div> Category:  {item.shopify_product_type}</div> : ''}
                                            </p>
                                            }
                                        </div>
                                    </Dropdown.Item>);
                                })}
                            </div>
                            <hr />
                            <Dropdown.Item className='f-center' id="add_product" href={`/content/products`}>
                                <FontAwesomeIcon type={'solid'} icon={'plus'} iconClass={'me-1'} />
                                Upload New Products
                            </Dropdown.Item>
                        </Dropdown.Menu>
                        :
                        <></>
                    }
                </Dropdown>
                <div className='position-relative'>
                <Form.Group className='mb-3'>
                    <Form.Control id="search_pro_" className={errorMsg ? 'is-invalid' : ''} placeholder="Search with multiple tags" onChange={this.product_tags_search} value={searchTag} autoComplete='off' />
                </Form.Group>
                {showtags && productTags ?
                    <Dropdown.Menu show={true} className='w-100'>
                        {productTags.map((tag, index) => {
                            return <Dropdown.Item className='f-center overflow-hidden' key={`prd_tags_${index}`} onClick={() => this.getData_bySelected_Tag(tag)}>
                                <p className='fs-10 text-muted text-ellipsis mb-0'>{tag}</p>
                            </Dropdown.Item>
                        })}
                    </Dropdown.Menu>
                    : ''}
                </div>
                <div className="mb-3 mb-xl-4">
                    <div className="tags-group d-flex align-items-center flex-wrap overflow-hidden w-100">
                        {selectedTag && selectedTag.length > 0 ? selectedTag.map((selectedTag, i) => {
                            return <Badge key={selectedTag.toString()} className="me-1 mb-1 px-3 py-2 f-center bg-secondary overflow-hidden">
                                <span className=' text-break text-wrap text-start'>{selectedTag}</span>
                                <Button variant='link' size='sm' className="p-0 ms-1 text-gray-800" onClick={() => this.removeTag(selectedTag)}>
                                    <FontAwesomeIcon type={'solid'} icon={'xmark'} />
                                </Button>
                            </Badge>
                        })
                            :
                            ''}
                    </div>
                </div>


                {modalData.postproduct && modalData.postproduct.length > 0 ?
                    modalData.postproduct.map((Item, index) => {
                        return <div key={index} className='p-2 box-shadow mb-2 rounded-2 d-flex align-items-center position-relative border'>
                            <span className='position-absolute start-0 translate-middle h-18px w-18px rounded-circle f-center bg-dark text-gray-100 top-50 fs-10 fw-medium' style={{ zIndex: 1 }}>{index + 1}</span>
                            <div className='d-flex flex-grow-1 align-items-center overflow-hidden' style={{maxWidth:'calc(100% - 32px)'}}>
                                <div className="w-44px d-flex flex-shrink-0 me-2">
                                    <ImageType mediaClass={'position-relative overflow-hidden w-100 border rounded-1'} size={100} height={44} width={44} url={Item?.product?.product_image} alt={""} isCover={true} />
                                </div>
                                <div className="d-flex align-items-start flex-column overflow-hidden w-100">
                                    <p className="fs-9 fw-medium mb-1 text-wrap text-break">{Item?.product.product_title}</p>
                                    <div className="d-flex align-items-center mb-1">
                                        {Item.product.product_discount != 0 && Item.product.product_discount != Item.product.product_price ?
                                            <span className='fs-8 fw-bold me-2'>{Item.product.discount_currency_symbol}{Math.trunc(Item.product.product_discount * 100) / 100}</span>
                                            :
                                            <span className='fs-8 fw-bold me-2'>{Item.product.discount_currency_symbol}{Math.trunc(Item.product.product_price * 100) / 100}</span>}
                                        {(Item.product.product_discount && Item.product.product_discount > 0) ? <span className='fs-8 text-muted' style={{ textDecoration: 'line-through' }}>{Item.product.discount_currency_symbol}{Item.product.product_discount > 0 ? Item.product.product_price : ''}</span> : ''}
                                    </div>
                                </div>
                            </div>
                            <Button variant="icon" size="sm" className="btn-active-danger" onClick={() => this.removeTaggProduct(Item.product.product_sku, Item.product.id, Item.post_id)}>
                                <FontAwesomeIcon type={'solid'} icon={'trash-can'}/>
                            </Button>
                        </div>
                    }) :
                    <div className='f-center py-3 text-gray-300 mb-10'>
                        <span className='h-40px w-40px rounded-circle me-2 border f-center fs-4'><FontAwesomeIcon type={'light'} icon={'bag-shopping'}/></span>
                        No Products Tagged Yet
                    </div>
                }
                <div>
                </div>
            </div>
            {/* <h3 className="p-4 fs-8 f-between mb-0 border-top">
                    <span className="fw-medium">Suggested Products</span>
                    <Badge bg='primary' className='rounded-pill bg-transparent text-primary border border-primary'>Ai-Recommended</Badge>
                </h3>
                <div className='mb-3'>
                    <div className='overflow-auto px-4 pt-1' style={{maxHeight:340}}>
                        <div class="p-2 box-shadow mb-2 rounded-2 d-flex align-items-center position-relative border">
                            <div className="d-flex flex-grow-1 align-items-center">
                                <div className="w-60px d-flex flex-shrink-0 me-2">
                                    <ImageType mediaClass={'position-relative overflow-hidden w-100 border rounded-1'} size={100} height={60} width={60} url={'https://lh3.googleusercontent.com/d/1ngv3ZqRrow25w-jc62ZlOaWbWGe6TkWi=s220?authuser=0'} alt={""} />
                                </div>
                                <div className="d-flex align-items-start flex-column overflow-hidden w-100">
                                    <p className="fs-9 fw-medium mb-1 text-wrap ">Apple iPhone 12</p>
                                    <div className="d-flex align-items-center tb_pro_price mb-1">
                                        <span className='fs-8 text-muted' style={{textDecoration:'line-through'}}>$1349</span>
                                    </div>
                                </div>
                            </div>
                            <Button variant="icon" size="sm" className="btn-active-primary rounded-circle h-24px w-24px">
                                <FontAwesomeIcon icon={'plus'}/>
                            </Button>
                        </div>
                        <div class="p-2 box-shadow mb-2 rounded-2 d-flex align-items-center position-relative border">
                            <div className="d-flex flex-grow-1 align-items-center">
                                <div className="w-60px d-flex flex-shrink-0 me-2">
                                    <ImageType mediaClass={'position-relative overflow-hidden w-100 border rounded-1'} size={100} height={60} width={60} url={'https://lh3.googleusercontent.com/d/1ngv3ZqRrow25w-jc62ZlOaWbWGe6TkWi=s220?authuser=0'} alt={""} />
                                </div>
                                <div className="d-flex align-items-start flex-column overflow-hidden w-100">
                                    <p className="fs-9 fw-medium mb-1 text-wrap ">Apple iPhone 12</p>
                                    <div className="d-flex align-items-center tb_pro_price mb-1">
                                        <span className='fs-8 text-muted' style={{textDecoration:'line-through'}}>$1349</span>
                                    </div>
                                </div>
                            </div>
                            <div className="bg-success text-gray-100 f-center rounded-circle h-24px w-24px">
                                <FontAwesomeIcon icon={'check'}/>
                            </div>
                        </div>
                    </div>
                </div> */}
        </div>
        )
    }
}



const mapStateToProps = (state) => {
    return {
        total_pages: state.products?.total_pages,
        searchProducts: state.products?.searchProducts,
        loadingTagProduct: state.products?.loadingTagProduct,
        moderation: state.moderation,
        ctaData: state.ctaData?.data,
        authorized: state.authorized,
    }
}


const mapDispatchToProps = dispatch => {
    return {
        SEARCH_PRODUCTS_TEXT: (text) => dispatch(({ type: SEARCH_BY_PRODUCTS, payload: text })),
        ACTION_PRODUCT_LIST_FORTAG: (page, showPost, tag, search, callBack, referenceId, newMedia) => dispatch(ACTION_PRODUCT_LIST_FORTAG(page, showPost, tag, search, callBack, referenceId, newMedia)),
        TAG_PRODUCT_ACTION: (product, referenceId, location_hotspot, hostpot, feedId, callBack) => dispatch(TAG_PRODUCT_ACTION(product, referenceId, location_hotspot, hostpot, feedId, callBack)),
        REMOVE_TAGGED_PRODUCT_ACTION: (product_sku, productId, referenceId) => dispatch(REMOVE_TAGGED_PRODUCT_ACTION(product_sku, productId, referenceId)),
        // ENABLE_HOTSPOT_ACTION: (id, referenceId, callBack) => dispatch(ENABLE_HOTSPOT_ACTION(id, referenceId, callBack)),
        getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
        fetchProductList: (page, showPost, tag, search, callBack, referenceId, newMedia) => dispatch(fetchProductList(page, showPost, tag, search, callBack, referenceId, newMedia)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(TagProducts);